<template>
    <div class="row">
        <div class="col-lg-8  me-auto">
            <div class="card w-100" style="margin-top: 30px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                    <!-- <h5 class="mb-0 text-white">Single Message</h5> -->
                </div>

                <div class="card-body">

                    <form @submit.prevent="sendMessage" class="px-lg-3">


                        <div class="mb-3">
                            <label for="agentID" class="form-label">Message</label>
                            <textarea v-model="form.message" rows="4" class="form-control"
                                placeholder="Write a message"></textarea>

                        </div>
                        <div v-if="form.is_verified" class="mb-4">
                            <label for="recipient_phone" class="form-label">Name</label>
                            <input disabled v-model="form.name" type="text" class="form-control" id="agent_name"
                                name="agent_name" >
                            
                        </div>

                        <div class="mb-4">
                            <label for="recipient_phone" class="form-label">Phone Number</label>
                            <input v-model="form.phoneNumber" type="text" class="form-control" id="agent_name"
                                name="agent_name" placeholder="Enter Phone Number">
                                <div v-if="isValidating" class="spinner mt-2"></div>
                        </div>

                        <div class="d-flex gap-3 flex-wrap flex-column-reverse flex-md-row justify-content-md-end">
                            <button @click="goBack" class="btn btn-outline-primary px-5 me-2">Cancel</button>
                            <button @click="shouldSchedule" data-bs-toggle="modal" data-bs-target="#verticalycentered"
                                type="button" class="btn btn-primary px-3 me-2" :disabled="!canSubmit"
                                style="background-color: #041b3f;">Schedule</button>
                            <button :disabled="!canSubmit" type="submit" class="btn btn-primary px-5"
                                style="background-color: #041b3f;">{{ btnState }}</button>
                        </div>
                    </form>

                </div>
            </div>
            <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            <!-- Vertically centered Modal -->
            <div class="modal fade" id="verticalycentered" tabindex="-1">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">Schedule a Message</h5>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <form @submit.prevent="sendMessage" class="modal-body">
                            <div class="mb-3">
                                <label for="date_time" class="form-label mb-2">Date & Time</label>
                                <input v-model="form.date_time" type="datetime-local" class="form-control"
                                    name="date_time" id="date_time">
                            </div>
                            <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            <button :disabled="!canSubmit && !form.shouldSchedule" class="btn btn-primary">{{ btnState }}</button>
                        </div>
                        </form>
                       
                    </div>
                </div>
            </div>
            <!-- End Vertically centered Modal-->
        </div>
    </div>
</template>
<script>
import { defineComponent } from 'vue'
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
export default defineComponent({
    components: { Modal },
    data() {
        return {
            isValidating: false,
            form: new Form({
                wallet_number: '',
                message: '',
                phoneNumber: '',
                name:'',
                date_time: '',
                is_verified: false,
                shouldSchedule: false,
            },{
                resetOnSuccess: false,
            }),
            shouldSchedule() {
                this.form.shouldSchedule = true
            },
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            btnState: 'Send'
        }
    },

    watch: {
        'form.phoneNumber': function (value) {
            this.form.wallet_number = value
            if (this.typingTimeout) clearTimeout(this.typingTimeout); 
            if(value.length < 4) this.form.is_verified = false;
            if (value.length > 3 && this.form.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.isValidating = true
                    this.form.post('/bank/verify-wallet-id')
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.isValidating = false;
                                this.is_verified = true;
                                this.form.name = data.walletName;
                                this.form.is_verified = true;
                            } else {
                                this.is_verified = false;
                                this.form.is_verified = false;
                                this.isValidating = false;
                                this.modal.type = 'failed',
                                this.modal.message = data.message,
                                this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            this.isValidating = false;
                            if (error.response && error.response.data && error.response.data.errors) {
                                this.form.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        },
        'form.date_time': function (value){
            if(value !== ''){ this.form.shouldSchedule = true}else{ this.form.shouldSchedule = false }
        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.message !== '' && this.form.is_verified == true && this.form.phoneNumber !== '' && this.form.processing != true
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        sendMessage() {
            this.btnState = `Sending...`
            this.form.post('/bank/agent-float')
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.btnState = `Sent`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.isValidating = false;
                        this.btnState = `Send`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Send`
                });
        },
    }
})
</script>
<style scoped>
.otp-input {
    width: 60px;
    height: 60px;
    font-size: 2rem;
    font-weight: bold;
}

.form-label {
    font-weight: bold;
}

.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
