<template>
    <div class="row">
        <div class="col-lg-8  me-auto">
            <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                    <h5 class="mb-0 text-white">Customer Details</h5>
                </div>
                <div class="card-body">
                    <form @submit.prevent id="updateForm">

                        <div v-if="!is_verified" class="form-group mb-3 ">
                            <label for="accountPhoneInput">Ac no or Phone no</label>
                            <div class="d-flex align-items-center ">
                                <div class="w-100">
                                    <input type="text" class="form-control" id="account_no" name="walletId"
                                        placeholder="Enter Phone No or Ac no" v-model="form.acountPhoneInput">
                                    <div v-if="isValidating" class="spinner mt-2"></div>
                                </div>


                            </div>
                        </div>
                        <div v-if="is_verified && on_edit === false" class="col-lg-10 mx-auto mb-3">
                            <div class="form-group mb-3">
                                <label for="nameInput">Wallet Name</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input disabled type="text" v-model="form.fullname" id="nameInput" name="name"
                                        class="form-control" placeholder="Enter Name">
                                </div>

                            </div>

                            <div class="form-group mb-3">
                                <label for="phoneNumber">Phone Number</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input v-model="form.acountPhoneInput" disabled type="text" id="phoneNumberInput"
                                        name="phoneNumber" class="form-control" placeholder="Enter Phone Number">
                                </div>

                            </div>
                            <div class="d-flex justify-content-between">
                                <button @click="cancel" class="btn btn-outline-primary">back</button>
                                <button @click="onEdit" class="btn btn-success">Edit</button>
                            </div>
                        </div>
                        <div v-if="on_edit" id="is_verified">
                            <div class="form-group mb-3">
                                <label for="nameInput">Wallet Name</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input type="text" v-model="form.fullname" id="nameInput" name="name"
                                        class="form-control" placeholder="Enter Name">
                                    <button @click="updateField('name', 'fullname')" id="name" type="button"
                                        class="btn btn-primary">Update</button>
                                </div>

                            </div>

                            <div class="form-group mb-3">
                                <label for="phoneNumber">Phone Number</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input v-model="form.phoneNumber" type="text" id="phoneNumberInput"
                                        name="phoneNumber" class="form-control" placeholder="Enter Phone Number">
                                    <button id="phoneNumber" type="button" class="btn btn-primary"
                                        @click="updateField('phoneNumber', 'phoneNumber')">Update</button>
                                </div>

                            </div>

                            <div class="form-group mb-3 ">
                                <label for="macAddress">Mac Address</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input type="text" v-model="form.macAddress" id="macAddressInput" name="macAddress"
                                        class="form-control" placeholder="Mac Address">
                                    <button id="macAddress" type="button" class="btn btn-primary"
                                        @click="updateField('macAddress', 'macAddress')">Update</button>
                                </div>

                            </div>

                            <div class="form-group mb-3 ">
                                <label for="language">Language</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <select v-model="form.language" class="form-select" name="language" id="gender">
                                        <option value="English">English</option>
                                        <option value="Somali">Somali</option>
                                        <!-- <option value="Arabic">Arabic</option> -->
                                    </select>
                                    <button id="language" type="button" class="btn btn-primary"
                                        @click="updateField('language', 'language')">Update</button>
                                </div>

                            </div>

                            <div class="form-group mb-3">
                                <label for="email">Email</label>
                                <div class="d-flex gap-2 d-flex align-items-center justify-content-center">
                                    <input v-model="form.email" type="email" id="emailInput" name="email"
                                        class="form-control" placeholder="Email">
                                    <button id="email" type="button" class="btn btn-primary"
                                        @click="updateField('email', 'email')">Update</button>
                                </div>


                            </div>
                        </div>
                        <div class="d-flex justify-content-between mt-2">
                            <a href="" class="btn btn-danger exit-btn">Exit</a>
                            <button v-if="on_edit" @click="done" class="btn btn-success">Done</button>
                            <button @click="verify" :disabled="isValidating" v-if="!on_edit && !is_verified"
                                class="btn btn-success">Verify</button>
                        </div>

                    </form>
                </div>
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>

            </div>

        </div>
    </div>
</template>

<script>
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
import flatpickr from 'flatpickr';
import FlatPickr from 'vue-flatpickr-component';
import { VueTelInput } from 'vue-tel-input';
import 'vue-tel-input/dist/vue-tel-input.css';
import 'flatpickr/dist/flatpickr.css';
export default {
    components: { Modal, flatpickr, FlatPickr, VueTelInput },
    props:{
        page_info:{
            type:Object,
            required: true
        }
    },
    data() {
        return {
            form: new Form({
                wallet_number: '',
                acountPhoneInput: '',
                is_verified: false,
                fullname: '',
                macAddress: '',
                phoneNumber: '',
                email: '',
                language: '',
                fullphone: '',

            }, {
                resetOnSuccess: false,
            }),
            toUpdateForm: new Form({
                value: '',
                walletId: '',
                fieldToChange: '',
                is_verified: ''
            }),
            selectedDate: null,
            dateConfig: {
                maxDate: new Date().setFullYear(new Date().getFullYear() - 18),
                dateFormat: 'Y-m-d',
                disableMobile: true,
                altInput: true,
                altFormat: 'F j, Y',
            },
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            is_verified: false,
            isValidating: false,
            on_edit: false,
            combinedPhoneNumber: '',
            countryCode: '',
            btnState: 'submit'
        };
    },
    watch: {
        'form.acountPhoneInput': function (value) {
            if (this.typingTimeout) clearTimeout(this.typingTimeout);
            this.form.wallet_number = value
            if (value.length > 3 && this.form.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.isValidating = true
                    this.form.post(`${this.page_info.domain}bank/verify-wallet-id`)
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.isValidating = false;
                                this.is_verified = true;
                                this.form.walletId = data.walletId;
                                this.form.groupId = data.groupId;
                                this.form.is_verified = true;
                                this.form.fullname = data.walletName;
                                this.form.phoneNumber = data.phoneNumber;
                                this.form.language = data.language;
                                this.form.macAddress = data.macAddress;
                                this.form.email = data.email;
                            } else {
                                this.is_verified = false;
                                this.form.is_verified = false;
                                this.isValidating = false;
                                this.modal.type = 'failed',
                                    this.modal.message = data.message,
                                    this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            this.cashOutIsValidating = false;
                            if (error.response && error.response.data && error.response.data.errors) {
                                this.cashInForm.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.fullname !== '' && this.form.processing === false
            );
        },
    },
    methods: {
        verify() {
            this.isValidating = true
            if (this.form.wallet_number !== '') {
                this.form.post(`${this.page_info.domain}bank/verify-wallet-id`)
                    .then((data) => {
                        if (data.status === "SUCCESS") {
                            this.isValidating = false;
                            this.is_verified = true;
                            this.form.walletId = data.walletId;
                            this.form.groupId = data.groupId;
                            this.form.is_verified = true;
                            this.form.fullname = data.walletName;
                            this.form.phoneNumber = data.phoneNumber;
                            this.form.language = data.language;
                            this.form.macAddress = data.macAddress;
                            this.form.email = data.email;

                        } else {
                            this.is_verified = false;
                            this.form.is_verified = false;
                            this.isValidating = false;
                            this.modal.type = 'failed',
                                this.modal.message = data.message,
                                this.modal.title = 'Not Found'
                            let bn = document.getElementById('modalBtn');
                            setTimeout(() => bn.click(), 200)
                        }
                    })
                    .catch((error) => {
                        this.isValidating = false;
                        if (error.response && error.response.data && error.response.data.errors) {
                            this.cashInForm.errors.record(error.response.data.errors);
                        } else {
                            this.$toast.error(error.response.data);
                        }
                    });
            } else {
                this.isValidating = false,
                    this.modal.type = 'failed',
                    this.modal.message = "Please supply an account number or phone number",
                    this.modal.title = 'Fill The Form'
                let bn = document.getElementById('modalBtn');
                setTimeout(() => bn.click(), 200)
            }

        },
        goBack() {
            window.history.back();
        },
        done() {
            this.on_edit = false
            this.is_verified = false
        },
        onEdit() {
            this.on_edit = true
            this.is_verified = true
        },
        cancel() {
            this.is_verified = false
        },
        formatPhoneNumber(phoneNumber, numberOjg) {
            console.log(numberOjg)
            this.combinedPhoneNumber = phoneNumber;
            this.form.phone = `${numberOjg.countryCallingCode}${numberOjg.nationalNumber
                }`
            this.countryCode = this.$refs.phoneInput.selectedCountry.callingCode;
        },
        createAccount() {

        },
        updateField(fieldName, formField) {
            this.toUpdateForm.value = this.form[formField]
            this.toUpdateForm.walletId = this.form.acountPhoneInput
            this.toUpdateForm.fieldToChange = fieldName
            this.toUpdateForm.is_verified = this.form.is_verified
            let btn = document.getElementById(fieldName);
            btn.innerHTML = `Updating...`;
            btn.disabled = true;
            this.btnState = `...`
            this.toUpdateForm.post(`${this.page_info.domain}bank/update-user`)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        btn.disabled = false;
                        btn.innerHTML = `Updated`;
                        this.modal.type = 'success',
                            this.modal.message = data.message,
                            this.modal.title = `Field Updated`
                        this.toUpdateForm.reset()
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                        btn.disabled = false;
                            this.modal.message = data.message,
                            this.modal.title = 'Field Failed To Update'
                        this.btnState = `Submit`
                        btn.innerHTML = `Update`;
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    btn.disabled = false;
                    this.form.is_verified = false;
                    this.btnState = `Submit`
                    btn.innerHTML = `Update`;
                });
        }
    },
};
</script>


<style scoped>
.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
