<template>
    <div class="row">
        <div class="col-lg-8  me-auto">

            <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                </div>
                <div class="card-body">
                    <form @submit.prevent="airtime_payment" class="px-lg-5">

                        <div class="mb-3">
                            <label for="serviceProviders" class="form-label">Available Options</label>
                            <select v-model="form.service_provider" name="service_provider" class="form-select" id="serviceProviders">
                                <option value="">Select Service Provider</option>
                                <option value="safaricom">Safaricom</option>
                                <option value="ETHIOTELECOM">ETHIO TELECOM</option>
                            </select>
                        </div>

                        <div class="mb-3">
                            <label for="phoneNumber" class="form-label">Phone Number</label>
                            <input v-model="form.phone_number" type="text" name="phone_number" class="form-control" placeholder="Enter Phone Number"
                                id="phoneNumber">

                        </div>

                        <div class="mb-3">
                            <label for="amount" class="form-label">Amount</label>
                            <input  v-model="form.amount" type="text" name="amount" class="form-control" placeholder="Enter Amount"
                                id="amount">

                        </div>

                        <div class="d-flex justify-content-end">
                            <a  @click="goBack" href="#" type="button" class="btn btn-outline-primary px-5 me-2">Cancel</a>
                            <button v-html="btnState" :disabled="!canSubmit" type="submit" class="btn btn-primary px-5"
                                style="background-color: #041b3f;"></button>
                        </div>
                    </form>

                </div>

                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>
        </div>

    </div>



</template>
<script>
import { defineComponent } from 'vue'
import Form from '@utils/form/Form'
import Modal from "./modal/Modal.vue";

export default defineComponent({
    name: 'AirtimePayment',
    components: { Modal },
    props:{
        routes:{
            type: Object,
            required: true,
        }
    },
    data() {
        return {
            isValidating: false,
            form: new Form({
                service_provider: '',
                phone_number: '',
                amount: ''
            }),
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            btnState: 'submit'
        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.amount !== '' && this.form.phone_number !== '' && this.form.processing != true &&  this.form.service_provider !== ''
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        airtime_payment() {
            this.btnState = `Submitting...`
            this.form.post(this.routes.airtime)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.isValidating = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Submit`
                });
        },
    }
})
</script>
<style scoped>
.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
