<template>
    <div class="row">
        <div class="col-lg-12">
            <div class="rounded-4">
                <div class="card-body row">
                    <div class="col-md-8 mb-4">
                        <div class="d-flex justify-content-center">
                            <div class="form-wrapper bg-white">
                                <div style="background: #041E43" class="p-3 px-5 text-light">
                                    <div class="title">
                                        <h3 class="text-white fw-bold"></h3>
                                    </div>

                                </div>

                                <form @submit.prevent="sendMessage" class="form-group row px-5 p-4 g-3" method="post"
                                    enctype="multipart/form-data">
                                   
                                    <div v-if="fileUploaded" class="accordion accordion-flush" id="accordionFlushExample">
                                        <div class="accordion-item">
                                            <h2 class="accordion-header">
                                                <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseTwo" aria-expanded="false" aria-controls="flush-collapseTwo">
                                                    Contacts List
                                                </button>
                                            </h2>
                                            <div id="flush-collapseTwo" class="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                                                <div class="accordion-body">
                                                    <div class="col-12">
                                                      
                                                        <div class="table-responsive">
                                                            <table class="table table-striped">
                                                                <thead class="thead-dark">
                                                                <tr style="background: #041E43" class="text-white">
                                                                    <th> <span>Name</span> </th>
                                                                    <th>Number</th>
                                                                </tr>
                                                                </thead>
                                                                <tbody>
                                                                    <tr :key="index" v-for="(contact,index) in form.contacts.slice(0, 10)">
                                                                        <td>{{ contact.name }}</td>
                                                                        <td>{{ contact.contact }}</td>
                                                                
                                                                </tr> 
                                                                
                                                               
                                                                </tbody>
                                                            </table>
                                                        </div>
                                                    </div></div>
                                            </div>
                                        </div>

                                    </div>

                                    <div class="col-12">
                                        <label for="contacts">Upload Contacts (CSV)</label>
                                        <input type="file" @change="handleFileUpload" class="form-control" id="contacts"
                                            accept=".csv">
                                    </div>

                                    <div class="col-12">
                                        <label for="message">Message</label>
                                        <textarea v-model="form.message" id="message" placeholder="Enter your message"
                                            class="form-control" cols="30" rows="3"></textarea>
                                    </div>

                                    <div class="col-lg-12 d-flex justify-content-between mt-3">
                                        <button @click="goBack" type="button"
                                            class="btn btn-outline-primary">Cancel</button>
                                        <button @click="sendMessage" :disabled="!canSubmit" type="submit" class="btn btn-primary">{{ btnState }}</button>
                                    </div>
                                </form>

                            </div>
                        </div>
                    </div>
                </div>

                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>

            </div>
        </div>
    </div>
</template>
<script>
import { defineComponent } from 'vue'
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
export default defineComponent({
    components: { Modal},
    data() {
        return {
            isValidating: false,
            form: new Form({
                contacts: '',
                wallet_number: '',
                message: '',
                phoneNumber: '',
                name: '',
                date_time: '',
                is_verified: false,
                shouldSchedule: false,
            }, {
                resetOnSuccess: false,
            }),
            fileUploaded: false,
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            btnState: 'Send Message'
        }
    },

    watch: {
        'form.phoneNumber': function (value) {
            this.form.wallet_number = value
            if (this.typingTimeout) clearTimeout(this.typingTimeout);
            if (value.length < 4) this.form.is_verified = false;
            if (value.length > 3 && this.form.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.isValidating = true
                    this.form.post('/bank/verify-wallet-id')
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.isValidating = false;
                                this.is_verified = true;
                                this.form.name = data.walletName;
                                this.form.is_verified = true;
                            } else {
                                this.is_verified = false;
                                this.form.is_verified = false;
                                this.isValidating = false;
                                this.modal.type = 'failed',
                                    this.modal.message = data.message,
                                    this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            this.isValidating = false;
                            if (error.response && error.response.data && error.response.data.errors) {
                                this.form.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        },
        'form.date_time': function (value) {
            if (value !== '') { this.form.shouldSchedule = true } else { this.form.shouldSchedule = false }
        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.message !== '' && this.fileUploaded == true && this.form.contacts.length !== 0 && this.form.processing != true
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        sendMessage() {
            this.btnState = `Sending...`
            this.form.post('/sms/bulk')
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your message is sent."
                        this.modal.title = 'Message Sent'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.btnState = `Message Sent`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Failed'
                        this.form.is_verified = false;
                        this.btnState = `Send Message`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Send Message`
                });
        },
        handleFileUpload(event) {
            const file = event.target.files[0];
            if (file && file.type === 'text/csv') {
                this.parseCSV(file);
            } else {
                alert('Please upload a valid CSV file.');
            }
        },
        parseCSV(file) {
            const reader = new FileReader();
            reader.onload = (e) => {
                const csvData = e.target.result;
                const parsedData = this.csvToArray(csvData)
                this.form.contacts = parsedData;
                this.fileUploaded = true

            };
            reader.readAsText(file);
        },
        csvToArray(csvString) {
            const rows = csvString.split('\n');
            const contacts = [];

            rows.forEach((row) => {
                const [name, contact] = row.split(',');
                if (name && contact) {
                    contacts.push({ name: name.trim(), contact: contact.trim() });
                }
            });

            return contacts;
        },
    }
})
</script>
<style scoped>
.otp-input {
    width: 60px;
    height: 60px;
    font-size: 2rem;
    font-weight: bold;
}

.form-label {
    font-weight: bold;
}

.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
