<template>

    <div class="row">
            <div class="col-lg-8">
                <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                    <div class="card-header text-white" style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                        <!-- <h5 class="mb-0 text-white">Cashier Account Refill</h5> -->
                    </div>
                    <div class="card-body">
                        <form action="" method="post" class="px-lg-3">
                     
                            <div class="mb-3">
                                <label for="agentID" class="form-label">Cashier ID</label>
                                <input type="text" class="form-control mb-3" id="cashierId" name="cashierId" placeholder="Enter Cashier Account ID" value="">
                               
                            </div>
                            <div class="mb-3">
                                <label for="amount" class="form-label">Amount</label>
                                <input type="text" class="form-control  mb-3" id="amount" name="amount" placeholder="Enter Amount">
                               
                            </div>
                            <div class="d-flex justify-content-end">
                                <a href="" class="btn btn-outline-primary px-5 me-2">Cancel</a>
                                <button type="submit" class="btn btn-primary px-5" style="background-color: #041b3f;">Submit</button>
                            </div>
                        </form>
                    </div>
                </div>

            </div>
        </div>

</template>
<script>
import { defineComponent } from 'vue'
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';

export default defineComponent({
    components: { Modal },
    data() {
        return {
            isValidating: false,
            form: new Form({
                agent_id: '',
                walletId: '',
                wallet_number: '',
                is_verified: false,
                agent_name: '',
                groupId: '',
                amount: ''
            }),
  
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            btnState: 'submit'
        }
    },

    watch: {
     
    },
    computed: {
        canSubmit() {
            return (
                this.form.amount !== '' && this.form.processing != true
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        agentFloat() {
            this.btnState = `Submitting...`
            this.form.post('/bank/agent-float')
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.isValidating = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Submit`
                });
        },
        cashOutToBanks(){
            alert('call')
            console.log(this.cashOutToBank)
        }
    }
})
</script>
