<template>
    <div class="row">
        <div class="col-lg-8  me-auto">
            <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">

                </div>

                <div class="card-body">

                    <form @submit.prevent="agentFloat" class="px-lg-3">
                        <!-- Agent No -->
                        <div class="mb-3">
                            <label for="account_no" class="form-label">Agent ID</label>
                            <input type="text" v-model="form.agent_id" class="form-control" id="account_no"
                                name="agentId" placeholder="Enter Agent ID">
                            <div v-if="isValidating" class="spinner mt-2"></div>
                        </div>


                        <div v-if="is_verified" class="mb-4">
                            <label for="recipient_name" class="form-label">Agent Name</label>
                            <input disabled type="text" v-model="form.agent_name" class="form-control" id="agent_name"
                                name="agent_name" placeholder="Enter Agent Name">

                        </div>

                        <div v-if="is_verified" class="mb-3">
                            <label for="amount" class="form-label">Amount</label>
                            <input type="text" v-model="form.amount" class="form-control" id="amount" name="amount"
                                placeholder="Enter Amount" value="">
                        </div>

                        <div class="d-flex gap-3 flex-wrap flex-column-reverse flex-md-row justify-content-md-end">
                            <a @click="goBack" href="#" class="btn btn-outline-primary px-5 me-2">Cancel</a>
                            <button data-bs-toggle="modal" data-bs-target="#verticalycentered" type="button"
                                class="btn btn-primary px-2 me-2" style="background-color: #041b3f;">Cashout to other
                                banks</button>
                            <button v-html="btnState" :disabled="!canSubmit" type="submit" class="btn btn-primary px-5"
                                style="background-color: #041b3f;"></button>
                        </div>
                    </form>

                </div>
                <!-- cash out to other banks Modal -->
                <div class="modal fade" id="verticalycentered" tabindex="-1">
                    <div class="modal-dialog modal-dialog-centered">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title">Cash Out</h5>
                                <button type="button" class="btn-close" data-bs-dismiss="modal"
                                    aria-label="Close"></button>
                            </div>
                            <form class="modal-body" @submit.prevent="cashOutToBanks">
                                <div class="mb-3">
                                    <label for="bank" class="form-label">Available Banks</label>
                                    <select v-model="cashOutToBank.bank"   name="bank" class="form-select " id="bank">
                                        <option value="Shabelle">Shabelle</option>
                                        <option value="Other">Others</option>
                                    </select>

                                </div>
                                <div class="mb-3">
                                    <label for="acc_no" class="form-label">Account Number</label>
                                    <input v-model="cashOutToBank.acc_no" type="text" name="acc_no" class="form-control" placeholder="Enter Amount"
                                        id="acc_no">
                                </div>
                                <div class="mb-3">
                                    <label for="amount" class="form-label">Amount</label>
                                    <input v-model="cashOutToBank.amount" type="text" name="amount" class="form-control" placeholder="Enter Amount"
                                        id="amount">
                                </div>
                                <div class="modal-footer">
                                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button class="btn btn-primary">Submit</button>
                            </div>
                            </form>
                        </div>
                    </div>
                </div>
                <!-- End cash out to other banks Modal-->

                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>

        </div>

    </div>
</template>
<script>
import { defineComponent } from 'vue'
import Form from '@utils/form/Form'
import Modal from "./modal/Modal.vue";

export default defineComponent({
    name: 'AgentFloat',
    props:{
        page_info:{
            type:Object
        }
    },
    components: { Modal },
    data() {
        return {
            isValidating: false,
            form: new Form({
                agent_id: '',
                walletId: '',
                wallet_number: '',
                is_verified: false,
                agent_name: '',
                groupId: '',
                amount: ''
            }),
            cashOutToBank: new Form({
                bank: 'Shabelle',
                acc_no: '',
                amount: ''

            }),
            is_verified: false,
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            btnState: 'submit'
        }
    },

    watch: {
        'form.agent_id': function (value) {
            this.form.wallet_number = value
            if (this.typingTimeout) clearTimeout(this.typingTimeout);

            if (value.length > 3 && this.form.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.isValidating = true
                    this.form.post(`${this.page_info?.domain}bank/verify-wallet-id`)
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.isValidating = false;
                                this.is_verified = true;
                                this.form.walletId = data.walletId;
                                this.form.groupId = data.groupId;
                                this.form.is_verified = true;
                                this.form.agent_name = data.walletName;
                                this.form.agent_id = data.walletId;
                            } else {
                                this.is_verified = false;
                                this.form.is_verified = false;
                                this.isValidating = false;
                                this.modal.type = 'failed',
                                    this.modal.message = data.message,
                                    this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            console.log(error);
                            this.isValidating = false;

                            if (error.response && error.response.data && error.response.data.errors) {
                                this.form.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        },
    },
    computed: {
        canSubmit() {
            return (
                this.form.amount !== '' && this.form.processing != true
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        agentFloat() {
            this.btnState = `Submitting...`
            this.form.post(`${this.page_info?.domain}bank/agent-float`)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                        this.modal.message = "Your payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.is_verified = false;
                        this.form.is_verified = false;
                        this.isValidating = false;
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Submit`
                });
        },
        cashOutToBanks(){
            alert('call')
            console.log(this.cashOutToBank)
        }
    }
})
</script>
<style scoped>
.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
