<template>
    <div>
        <div v-if="loaded" class="d-lg-flex align-items-center gap-2">

            <apexchart type="donut" height="350" :options="chartOptions" :series="series"></apexchart>

            <div>
                <div>
                    <span style="width: 5px;height: 5px;" class="badge bg-warning me-1 rounded-circle"></span>
                    <span>
                        <div>Failed Transaction</div>
                        <div class="fs-24">{{ series[1] }} K</div>
                    </span>
                </div>
                <div>
                    <span class="badge-success p-2 w-2 h-2"></span>
                    <span>
                        <div>Successful Transaction</div>
                        <div class="fs-24">{{ series[0] }} K</div>
                    </span>
                </div>
            </div>
        </div>
        <div v-else class="loader mx-auto"></div>
    </div>

</template>

<script>
import VueApexCharts from 'vue-apexcharts';
import axios from 'axios';

export default {
    components: {
        apexchart: VueApexCharts,
    },
    data() {
        return {
            series: [],
            loaded: false,
            chartOptions: {
                labels: ['Successful Transaction', 'Failed Transaction'],
                chart: {
                    type: 'donut',
                },
                stroke: {
                    width: 2,
                },
                dataLabels: {
                    enabled: true,
                },
                tooltip: {
                    y: {
                        formatter: function (val) {
                            return val + 'k'; 
                        },
                    },
                },
                legend: {
                    position: 'bottom', 
                },
            },
        };
    },
    methods: {
        async fetchTransactionData() {
            try {
                const response = await axios.get('/admin-dashboard/weekly-transactions');
                const data = response.data;
                this.updateChart(data);
                this.loaded = true;
            } catch (error) {
                console.error('Error fetching transaction data:', error);
            }
        },
        updateChart(data) {
            this.series = [(data.series.successful / 1000), (data.series.failed/1000)];
        },
    },
    mounted() {
        this.fetchTransactionData();
    },
};
</script>

<style scoped>
.loader {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
</style>