<template>
    <div>
        <apexchart v-if="loaded" type="line" height="350" :options="chartOptions" :series="series"></apexchart>

        <div v-else class="loader mx-auto"></div>

    </div>
</template>

<script>
   import VueApexCharts from 'vue-apexcharts';
import axios from 'axios';

export default {
    components: {
        apexchart: VueApexCharts,
    },
    data() {
        return {
            series: [], 
            xAxisLabels: [],
            loaded: false,
            chartOptions: {
                chart: {
                    type: 'line',
                    height: 350,
                },
                stroke: {
                    width: 5,
                    curve: 'smooth',
                },
                markers: {
                    size: 4,
                    hover: {
                        size: 6,
                    },
                },
                xaxis: {
                    categories: [], 
                    title: {
                        text: 'Date',
                    },
                },
                yaxis: {
                    title: {
                        text: 'Transactions',
                    },
                    labels: {
                        formatter: function (val) {
                            return val + 'k';
                        },
                    },
                },
                grid: {
                    borderColor: '#e7e7e7',
                    strokeDashArray: 5,
                },
                dataLabels: {
                    enabled: false,
                },
                tooltip: {
                    y: {
                        formatter: function (val) {
                            return val + ' transactions' 
                        },
                    },
                },
            },
        };
    },
    methods: {
        async fetchTransactionData() {
            try {
                const response = await axios.get('/teller-dashboard/weekly-transanctions');
                const data = response.data;
                this.updateChart(data);
                this.loaded = true;
            } catch (error) {
                console.error('Error fetching transaction data:', error);
            }
        },
        updateChart(data) {
           
            this.xAxisLabels = data.categories; 
            this.series = data.series.map(serviceType => ({
                name: serviceType.name,
                data: serviceType.data.map(amount => amount / 1000), 
            }));
           
            this.chartOptions.xaxis.categories = this.xAxisLabels;
        },
    },
    mounted() {
        // Fetch the data when the component is mounted
        this.fetchTransactionData();
    },
};

</script>
<style scoped>
.loader {
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

</style>