<template>
    <div class="modal modal-blur fade" id="modal-success" tabindex="-1" role="dialog" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered px-5">
            <div class="modal-content">
                <div class=" d-flex justify-content-end">
                    <!-- Close button -->
                    <button type="button" class="btn-close m-2" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <!-- Modal Header -->
                <div class="modal-header border-0">
                    <div class="text-center w-100">
                        <!-- Success icon -->
                        <div :class="`display-5 ${type === 'success' ? 'text-success' : 'text-danger'}  mb-3`">
                            <i :class="`bi ${type === 'success' ? 'bi-check-circle' : 'bi-x-circle'}`"></i>
                        </div>
                        <!-- Title -->
<!--                        <h5 class="modal-title" id="paymentSuccessLabel">Payment succeeded</h5>-->
                        <h5 class="modal-title" id="paymentSuccessLabel">{{ title}}</h5>
                    </div>

                </div>

                <!-- Modal Body -->
                <div class="modal-body text-center">
<!--                    <p>Your cash in payment of has been successfully submitted.</p>-->
                    <p>{{ message}}</p>
                </div>

                <!-- Modal Footer -->
                <div class="modal-footer justify-content-between">
                    <a href="/bank" class="btn btn-secondary" >Go to dashboard</a>
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        message:{
            type:String,
            required: true,
            default: ()=> ''
        },
        title:{
            type:String,
            required: true,
            default: ()=> ''
        },
        type:{
            type:String,
            required: true,
            default: ()=> ''
        }
    },
}

</script>
