<template>
    <div class="row">
        <div class="col-lg-10  me-auto">
            <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                    <h5 class="mb-0 text-white">Account Details</h5>
                </div>
                <div class="card-body">
                    <form @submit.prevent="createAccount" class="px-3 row" enctype="multipart/form-data">
                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="role" class="form-label">Select Group</label>
                            <select v-model="form.role" class="form-select " name="role" id="role">
                                <option value="Customer">Customer</option>
                                <!-- <option value="Merchant">Merchant</option> -->
                                <option value="Teller">Teller</option>
                                <option value="Agent">Agent</option>
                            </select>
                            <span v-if="form.errors.has('role')" class="text-danger"
                                v-text="form.errors.first('role')" />
                        </div>

                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="fullname" class="form-label">Full Name</label>
                            <input v-model="form.fullname" type="text" class="form-control" name="fullname"
                                id="fullname" placeholder="Enter Full Name">
                            <span v-if="form.errors.has('fullname')" class="text-danger"
                                v-text="form.errors.first('fullname')" />
                        </div>
                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="dob" class="form-label">Date of Birth</label>
                            <flat-pickr v-model="form.dob" :config="dateConfig" class="form-control"
                                placeholder="Select birthdate" />
                            <span v-if="form.errors.has('dob')" class="text-danger" v-text="form.errors.first('dob')" />
                        </div>

                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="phone" class="form-label">Phone Number</label>
                            <vue-tel-input v-model="form.fullphone" @input="formatPhoneNumber" :enable-formatting="true"
                                :default-country="'et'" :mode="international" @country-changed="onCountryChange"
                                ref="phoneInput" placeholder="Enter your phone number"></vue-tel-input>
                            <span v-if="form.errors.has('fullphone')" class="text-danger"
                                v-text="form.errors.first('fullphone')" />
                        </div>
                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="documentId" class="form-label">Document Type</label>
                                <select v-model="form.docType" class="form-select" name="gender" id="gender">
                                <option value="NationalId">National ID</option>
                                <option value="Passport">Passport</option>
                                <option value="Other">Other</option>
                            </select>
                            <span v-if="form.errors.has('docType')" class="text-danger"
                                v-text="form.errors.first('docType')" />
                        </div>
                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="documentId" class="form-label">Document ID</label>
                            <input v-model="form.documentId" type="text" class="form-control" name="documentId"
                                id="documentId" placeholder="Enter Document">
                            <span v-if="form.errors.has('documentId')" class="text-danger"
                                v-text="form.errors.first('documentId')" />
                        </div>
                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="email" class="form-label">Email Address</label>
                            <input v-model="form.email" type="email" class="form-control" name="email" id="email"
                                placeholder="Enter Email Address" value="">

                            <span v-if="form.errors.has('email')" class="text-danger"
                                v-text="form.errors.first('email')" />

                        </div>

                        <div class="col-lg-6 col-md-6 mb-3">
                            <label for="gender" class="form-label">Gender</label>
                            <select v-model="form.gender" class="form-select" name="gender" id="gender">
                                <option value="Male">Male</option>
                                <option value="Female">Female</option>
                                <option value="Female">Other</option>
                            </select>

                            <span v-if="form.errors.has('gender')" class="text-danger"
                                v-text="form.errors.first('gender')" />

                        </div>
                        <!-- TellerController-specific fields -->
                        <div v-if="agent || teller" class="row">
                            <!-- <div class="col-lg-6 col-md-6 mb-3">
                                <label for="bussinessname" class="form-label">Business Name</label>
                                <input v-model="form.bussinessname" type="text" class="form-control" name="bussinessname" id="bussinessname"
                                    placeholder="Enter Business Name">

                            </div> -->

                            <!-- <div class="col-lg-6 col-md-6 mb-3">
                                <label for="kebele" class="form-label">Kebele</label>
                                <input v-model="form.kebele" type="text" class="form-control" name="kebele" id="kebele"
                                    placeholder="Enter Kebele" value="">

                            </div> -->

                            <div class="col-lg-6 col-md-6 mb-3">
                                <label for="trade_license" class="form-label">Trade License</label>
                                <input v-model="form.trade_license" type="text" class="form-control"
                                    name="trade_license" id="trade_license" placeholder="Enter Trade License">
                                <span v-if="form.errors.has('trade_license')" class="text-danger"
                                    v-text="form.errors.first('trade_license')" />
                            </div>

                            <div class="col-lg-6 col-md-6 mb-3">
                                <label for="branch" class="form-label">Branch Name</label>
                                <input v-model="form.branch" type="text" class="form-control" name="branch" id="branch"
                                    placeholder="Enter Branch Name">
                                <span v-if="form.errors.has('branch')" class="text-danger"
                                    v-text="form.errors.first('branch')" />
                            </div>

                            <!-- <div  v-if="agent" class="col-lg-6 col-md-6 mb-3">
                                <label for="woreda" class="form-label">Woreda</label>
                                <input v-model="form.woreda" type="text" class="form-control" name="woreda" id="woreda"
                                    placeholder="Enter Woreda">

                            </div> -->
                        </div>
                        <div class="d-flex justify-content-end">
                            <button @click="goBack" type="button"
                                class="btn btn-outline-primary px-5 me-2">Cancel</button>
                            <button v-html="btnState" :disabled="!canSubmit" type="submit" class="btn btn-primary px-5"
                                style="background-color: #041b3f;"></button>
                        </div>
                    </form>
                </div>
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>

        </div>
    </div>
</template>

<script>
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
import flatpickr from 'flatpickr';
import FlatPickr from 'vue-flatpickr-component';
import { VueTelInput } from 'vue-tel-input';
import 'vue-tel-input/dist/vue-tel-input.css';
import 'flatpickr/dist/flatpickr.css';
export default {
    components: { Modal, flatpickr, FlatPickr, VueTelInput },
    data() {
        return {
            agent: false,
            teller: false,
            form: new Form({
                role: 'Customer',
                fullname: '',
                dob: '',
                phone: '',
                email: '',
                gender: '',
                documentId: '',
                bussinessname: '',
                fullphone: '',
                docType: '',
                kebele: '',
                branch: '',
                woreda: '',
                trade_license: '',
            }, {
                resetOnSuccess: false,
            }),
            selectedDate: null,
            dateConfig: {
                maxDate: new Date().setFullYear(new Date().getFullYear() - 18),
                dateFormat: 'Y-m-d',
                disableMobile: true,
                altInput: true,
                altFormat: 'F j, Y',
            },
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            combinedPhoneNumber: '',
            countryCode: '',
            btnState: 'submit'
        };
    },
    watch: {
        'form.role': function (value) {
            if (value == 'Teller') {
                this.teller = true
                this.agent = false
            }
            else if (value == 'Agent') {
                this.teller = false
                this.agent = true
            }
            else {
                this.teller = false
                this.agent = false
            }

        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.fullname !== '' && this.form.processing === false
            );
        },
    },
    methods: {
        goBack() {
            window.history.back();
        },
        formatPhoneNumber(phoneNumber, numberOjg) {
            console.log(numberOjg)
            this.combinedPhoneNumber = phoneNumber;
            this.form.phone = `${numberOjg.countryCallingCode}${numberOjg.nationalNumber
                }`
            this.countryCode = this.$refs.phoneInput.selectedCountry.callingCode;
        },
        createAccount() {
            this.btnState = `Submitting...`
            this.form.post('/bank/create-customer')
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = data.message,
                            this.modal.title = 'Account Created'
                        this.form.reset()
                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Account Not Created'

                        this.btnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.is_verified = true;
                    this.form.is_verified = true;
                    this.btnState = `Submit`
                });
        },
    },
};
</script>


<style scoped></style>
