<template>

    <div class="row">
        <div class="col-lg-8  me-auto">
            <div class="card w-100" style="margin-top: 50px; border-radius: 10px;">
                <div class="card-header text-white"
                    style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                    <!-- <h5 class="mb-0 text-white">GROUP MANAGEMENT</h5> -->
                </div>
                <div class="card-body">
                    <form @submit.prevent="changeGroup">

                        <div class="mb-3">
                            <label for="recipientName" class="form-label">Phone no or Ac no</label>
                            <input :disabled="is_verified" v-model="form.user_id" type="text" class="form-control mb-3" id="user_id" name="user_id"
                                placeholder="Enter Phone no or Ac no">
                                <div v-if="isValidating" class="spinner mt-2"></div>
                        </div>
                        <div v-if="is_verified">
                            <div class="mb-4" id="recipient_name_field_group_manage">
                            <label for="recipient_name_input_manage" class="form-label">Customer
                                Name</label>
                            <input type="text" class="form-control " id="recipient_name_input_manage"
                                name="recipient_name" disabled v-model="form.recipient_name" placeholder="Enter Recipient Name">

                        </div>

                        <div class="mb-3" id="current_group_field">
                            <label for="current_group" class="form-label">Current Group</label>
                            <select v-model="form.current_group" disabled id="current_group" class="form-select" name="new_group">

                                <option v-if="groups.length === 0" value="0">No group available</option>
                                <option :key="group.id" v-for="group in groups" :value="group.id">{{ group.name }}</option>
                            </select>
                        </div>

                        <div class="mb-3" id="new_group_field">
                            <label for="role" class="form-label">New Group</label>
                            <select v-model="form.new_group" class="form-select" name="new_group" id="role">
                                <option v-if="groups.length === 0" value="0">No group available</option>
                                <option :key="group.id" v-for="group in groups" :value="group.id">{{ group.name }}</option>

                            </select>

                        </div>
                        </div>

                        <div class="d-flex justify-content-end">
                            <button @click="goBack" type="button" class="btn btn-outline-primary px-5 me-2">Cancel</button>
                            <button :disabled="!canSubmit" class="btn btn-primary px-5" style="background-color: #041b3f;">{{ btnState }}</button>
                        </div>

                    </form>

                </div>
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>

        </div>
    </div>
</template>

<script>
import Form from '@utils/form/Form'
import Modal from '../payments/modal/Modal.vue';
import axios from 'axios';
export default {
    components: { Modal },
    props: {
        page_info:{
            type:Object
        }
    },
    data() {
        return {
            form: new Form({
                wallet_number: '',
                user_id: '',
                is_verified: false,
                recipient_name: '',
                current_group: '',
                new_group: '',
            }, {
                resetOnSuccess: false,
            }),
            groups: [],
            modal: {
                message: 'Unexpected Error Occurred',
                type: 'failed',
                title: 'Error'
            },
            is_verified: false,
            isValidating: false,
            on_edit: false,

            btnState: 'submit'
        };
    },
    watch: {
        'form.user_id': function (value) {
            if (this.typingTimeout) clearTimeout(this.typingTimeout);
            this.form.wallet_number = value
            if (value.length > 3 && this.form.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.isValidating = true
                    this.form.post(`${this.page_info?.domain}bank/verify-wallet-id`)
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.isValidating = false;
                                this.is_verified = true;
                                this.form.walletId = data.walletId;
                                this.form.current_group = data.groupId;
                                this.form.is_verified = true;
                                this.form.recipient_name = data.walletName;

                            } else {
                                this.is_verified = false;
                                this.form.is_verified = false;
                                this.isValidating = false;
                                this.modal.type = 'failed',
                                this.modal.message = data.message,
                                this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            this.cashOutIsValidating = false;
                            if (error.response && error.response.data && error.response.data.errors) {
                                this.cashInForm.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        }
    },
    computed: {
        canSubmit() {
            return (
                this.form.user_id !== '' && this.form.processing === false && this.form.new_group !== ''
            );
        },
    },
    mounted() {
        this.getGroups()
    },
    methods: {
        changeGroup() {
            // this.isValidating = true
            this.btnState= "submitting..."
            this.form.post(`${this.page_info?.domain}bank/group-management`)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.isValidating = false;
                        this.is_verified = false;
                        this.btnState = 'Submit'
                        this.form.reset()
                        this.modal.type = 'success',
                        this.modal.message = data.message,
                        this.modal.title = 'Group Changed'
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.btnState = 'Submit'
                        this.is_verified = true;
                        this.form.is_verified = true;
                        this.isValidating = false;
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Not Found'
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.isValidating = false;
                    this.btnState = 'Submit'
                    if (error.response && error.response.data && error.response.data.errors) {
                        this.cashInForm.errors.record(error.response.data.errors);
                    } else {
                        this.$toast.error(error.response.data);
                    }
                });


        },
        goBack() {
            window.history.back();
        },
        getGroups() {
            let apiUrl = `${this.page_info?.domain}bank/get-groups`
            axios.get(apiUrl)
                .then(response => {
                    let data = response.data;
                    this.groups = data
                })
                .catch(error => {
                    this.modal.type = 'failed',
                    this.modal.message = "failed to load groups",
                    this.modal.title = 'Error Occurred'
                    let bn = document.getElementById('modalBtn');
                    setTimeout(() => bn.click(), 200)
                    setTimeout(()=>window.location.reload(),5000)

                });
        }

    },
};
</script>


<style scoped>
.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
