<template>
    <div class="row">

        <div class="col-lg-8  me-auto">
            <ul class="nav nav-pills mb-4" id="pills-tab" role="tablist">
                <li class="nav-item" role="presentation">
                    <button style="margin-right:1rem" class="nav-link p-2 active" id="pills-home-tab"
                        data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab"
                        aria-controls="pills-home" aria-selected="true">CASH IN
                    </button>
                </li>
                <li class="nav-item" role="presentation">
                    <button style="" class="nav-link" id="pills-profile-tab" data-bs-toggle="pill"
                        data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile"
                        aria-selected="false">CASH OUT
                    </button>
                </li>
            </ul>
            <div class="tab-content" id="myTabContent">
                <div class="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="home-tab">
                    <div class="card w-100" style="border-radius: 10px;">
                        <div class="card-header text-white"
                            style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                            <h5 class="mb-0 text-white">CASH IN</h5>
                        </div>
                        <div class="card-body">
                            <form @submit.prevent="cashIn" class="px-4" id="walletForm">

                                <div class="mb-3">
                                    <label for="wallet_number" class="form-label">Phone No or Ac no</label>
                                    <input type="text" class="form-control" id="account_no" name="walletId"
                                        placeholder="Enter Phone No or Ac no" v-model="cashInForm.wallet_number">
                                    <div v-if="cashInIsValidating" class="spinner mt-2"></div>
                                </div>

                                <!-- Customer's Name -->
                                <div v-if="cash_in_is_verified" class="mb-3">
                                    <label for="customer_name" class="form-label">Recipient Name</label>
                                    <input disabled type="text" class="form-control" id="customer_name"
                                        placeholder="Enter customer name" v-model="cashInForm.recipientName">
                                </div>
                                <!-- Amount -->
                                <div v-if="cash_in_is_verified" class="mb-3">
                                    <label for="cash-in-amount" class="form-label">Amount</label>
                                    <input type="text" class="form-control" id="cash-in-amount"
                                        placeholder="Enter Amount" v-model="cashInForm.amount">
                                </div>

                                <div class="d-flex justify-content-end">
                                    <a  @click="goBack" href="#" class="btn btn-outline-primary px-5 me-2">Cancel</a>
                                    <button :disabled="!canSubmitCashIn" type="submit" v-html="cashInBtnState"
                                        class="btn btn-primary px-5" style="background-color: #041b3f;">
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
                <div class="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="profile-tab">
                    <div class="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="home-tab">
                        <div class="card w-100" style="border-radius: 10px;">
                            <div class="card-header text-white"
                                style="background-color: #041b3f; border-top-left-radius: 10px; border-top-right-radius: 10px;">
                                <h5 class="mb-0 text-white">CASH OUT</h5>
                            </div>
                            <div class="card-body">
                                <form @submit.prevent="cashOut" class="px-4">

                                    <div class="mb-3">
                                        <label for="wallet_number" class="form-label">Phone No or Ac no</label>
                                        <input type="text" class="form-control" id="account_no" name="walletId"
                                            placeholder="Enter Phone No or Ac no" v-model="cashOutForm.wallet_number">
                                        <div v-if="cashOutIsValidating" class="spinner mt-2"></div>
                                    </div>

                                    <!-- Customer's Name -->
                                    <div v-if="cash_out_is_verified" class="mb-3">
                                        <label for="customer_name" class="form-label">Recipient Name</label>
                                        <input disabled type="text" class="form-control" id="customer_name"
                                            placeholder="Enter customer name" v-model="cashOutForm.recipientName">
                                    </div>
                                    <!-- Amount -->
                                    <div v-if="cash_out_is_verified" class="mb-3">
                                        <label for="cash-in-amount" class="form-label">Amount</label>
                                        <input type="text" class="form-control" id="cash-in-amount"
                                            placeholder="Enter Amount" v-model="cashOutForm.amount">
                                    </div>

                                    <div class="d-flex justify-content-end">
                                        <a @click="goBack" href="#" class="btn btn-outline-primary px-5 me-2">Cancel</a>
                                        <button :disabled="!canSubmitCashOut" type="submit" v-html="cashOutBtnState"
                                            class="btn btn-primary px-5" style="background-color: #041b3f;">
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>

                </div>
                <!--modal-->
            
                <a style="visibility: hidden" id="modalBtn" href="#" class="btn" data-bs-toggle="modal"
                    data-bs-target="#modal-success">
                    Success modal
                </a>
                <!-- Vertically centered scrollable modal -->
                <modal :message="modal.message" :title="modal.title" :type="modal.type"></modal>
            </div>
        </div>

    </div>
</template>
<script>
import Form from '@utils/form/Form'
import Modal from "./modal/Modal.vue";

export default {
    props: {
        routes: {
            type: Object,
            required: true,
            default: () => { }
        }
    },
    components: { Modal },
    data() {
        return {
            cashInIsValidating: false,
            cash_in_is_verified: false,
            cashInBtnState: 'Submit',
            cashInForm: new Form({
                wallet_number: '',
                walletId: '',
                is_verified: false,
                recipientName: '',
                groupId: '',
                amount: '',
            }),
            cashOutIsValidating: false,
            cash_out_is_verified: false,
            cashOutBtnState: 'Submit',
            cashOutForm: new Form({
                wallet_number: '',
                walletId: '',
                is_verified: false,
                recipientName: '',
                groupId: '',
                amount: '',
            }),
            modal: {
                message: 'Make payment',
                type: 'failed',
                title: 'Error'
            }

        };
    },
    mounted(){
        if(location.hash == '#cash-out'){
            document.querySelector("#pills-profile-tab").click()
        }
    },
    computed: {
        canSubmitCashIn() {
            return (
                this.cashInForm.amount !== '' && this.cashInForm.processing != true
            );
        },
        canSubmitCashOut() {
            return (
                this.cashOutForm.amount !== '' && this.cashOutForm.processing != true
            );
        },
    },
    watch: {
        'cashInForm.wallet_number': function (value) {
            if (this.typingTimeout) clearTimeout(this.typingTimeout);

            if (value.length > 3 && this.cashInForm.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.cashInIsValidating = true
                    this.cashInForm.post('/bank/verify-wallet-id')
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.cashInIsValidating = false;
                                this.cash_in_is_verified = true;
                                this.cashInForm.walletId = data.walletId;
                                this.cashInForm.groupId = data.groupId;
                                this.cashInForm.is_verified = true;
                                this.cashInForm.recipientName = data.walletName;
                                this.cashInForm.wallet_number = data.walletId;
                            } else {
                                this.cash_in_is_verified = false;
                                this.cashInForm.is_verified = false;
                                this.cashInIsValidating = false;
                                this.modal.type = 'failed',
                                    this.modal.message = data.message,
                                    this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            console.log(error);
                            this.cashInIsValidating = false;

                            if (error.response && error.response.data && error.response.data.errors) {
                                this.cashInForm.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        },
        'cashOutForm.wallet_number': function (value) {
            if (this.typingTimeout) clearTimeout(this.typingTimeout);

            if (value.length > 3 && this.cashOutForm.is_verified === false) {
                this.typingTimeout = setTimeout(() => {
                    this.cashOutIsValidating = true
                    this.cashOutForm.post('/bank/verify-wallet-id')
                        .then((data) => {
                            if (data.status === "SUCCESS") {
                                this.cashOutIsValidating = false;
                                this.cash_out_is_verified = true;
                                this.cashOutForm.walletId = data.walletId;
                                this.cashOutForm.groupId = data.groupId;
                                this.cashOutForm.is_verified = true;
                                this.cashOutForm.recipientName = data.walletName;
                                this.cashOutForm.wallet_number = data.walletId;
                            } else {
                                this.cash_out_is_verified = false;
                                this.cashOutForm.is_verified = false;
                                this.cashOutIsValidating = false;
                                this.modal.type = 'failed',
                                    this.modal.message = data.message,
                                    this.modal.title = 'Not Found'
                                let bn = document.getElementById('modalBtn');
                                setTimeout(() => bn.click(), 200)
                            }
                        })
                        .catch((error) => {
                            this.cashOutIsValidating = false;
                            if (error.response && error.response.data && error.response.data.errors) {
                                this.cashInForm.errors.record(error.response.data.errors);
                            } else {
                                this.$toast.error(error.response.data);
                            }
                        });
                }, 1500);
            }
        }
    },
    methods: {
        goBack() {
            window.history.back();
        },
        cashIn() {
            this.cashInBtnState = `Submitting...`
            this.cashInForm.post(this.routes.cash_in_route)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your cash-in payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.cash_in_is_verified = false;
                        this.cashInForm.is_verified = false;
                        this.cashInBtnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.cash_in_is_verified = false;
                        this.cashInForm.is_verified = false;
                        this.cashInIsValidating = false;
                        this.cashInBtnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.response = true
                    this.responseMessage = "An error occurred when connecting";
                    this.cashInIsValidating = false;
                    this.cash_in_is_verified = true;
                    this.cashInForm.is_verified = true;
                    this.cashInBtnState = `Submit`
                });
        },
        cashOut() {
            this.cashOutBtnState = `Submitting...`
            this.cashOutForm.post(this.routes.cash_out_route)
                .then((data) => {
                    if (data.status === "SUCCESS") {
                        this.modal.type = 'success',
                            this.modal.message = "Your cash-out payment of has been successfully submitted."
                        this.modal.title = 'Payment succeeded'
                        this.cash_out_is_verified = false;
                        this.cashOutForm.is_verified = false;
                        this.cashOutBtnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    } else {
                        this.modal.type = 'failed',
                            this.modal.message = data.message,
                            this.modal.title = 'Payment not succeeded'
                        this.cash_out_is_verified = false;
                        this.cashOutForm.is_verified = false;
                        this.cashInIsValidating = false;
                        this.cashOutBtnState = `Submit`
                        let bn = document.getElementById('modalBtn');
                        setTimeout(() => bn.click(), 200)
                    }
                })
                .catch((error) => {
                    this.response = true
                    this.responseMessage = "An error occurred when connecting";
                    this.cashInIsValidating = false;
                    this.cash_out_is_verified = true;
                    this.cashOutForm.is_verified = true;
                    this.cashOutBtnState = `Submit`
                });
        },
    },
};
</script>
<style scoped>
.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-left-color: #041b3f;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
</style>
