<template>
    <div>
        <apexchart type="bar" height="350" :options="chartOptions" :series="series"></apexchart>
    </div>
</template>
<!-- <template>
    <div>
      <label for="startDate">Start Date:</label>
      <input type="date" id="startDate" v-model="startDate" />
      
      <label for="endDate">End Date:</label>
      <input type="date" id="endDate" v-model="endDate" />
      
      <button @click="fetchData">Load Data</button>
  
      <apexchart type="bar" height="350" :options="chartOptions" :series="series"></apexchart>
    </div>
  </template> -->

<script>
import axios from 'axios';
import VueApexCharts from 'vue-apexcharts';
import dayjs from 'dayjs';

export default {
    components: {
        apexchart: VueApexCharts,
    },
    data() {
        return {
            series: [],
            chartOptions: {
                chart: {
                    type: 'bar',
                    height: 350
                },
                plotOptions: {
                    bar: {
                        horizontal: false,
                        columnWidth: '45%',
                        endingShape: 'rounded'
                    },
                },
                dataLabels: {
                    enabled: false
                },
                stroke: {
                    show: true,
                    width: 2,
                    colors: ['transparent']
                },
                xaxis: {
                    categories: [],
                },
                yaxis: {
                    title: {
                        text: 'Amount (k)',
                        style: {
                            color: '#000',
                            fontWeight: 600
                        }
                    }
                },
                fill: {
                    opacity: 1,
                    colors: ['#0000FF']  // Blue color for bars
                },
                tooltip: {
                    y: {
                        formatter: function (val) {
                            return (val / 1000) + " k";  // Tooltip values in 'k'
                        }
                    }
                },
            }
        };
    },
    mounted() {
        this.fetchData()
    },
    methods: {
        fetchData() {
            let apiUrl;
            let xAxisCategories = [];
            apiUrl = `/fraud/high-value-violation?startDate=${this.startDate}&endDate=${this.endDate}`;


            axios.get(apiUrl)
                .then(response => {
                    let data = response.data;
                    console.log(data)
                    this.series = [{
                        name: 'High value transaction',
                        data: data.series
                    }];

                    this.chartOptions.xaxis.categories = data.categories;
                })
                .catch(error => {
                    console.error("Error fetching data:", error);
                    // alert("Failed to load data from the API.");
                });
        }
    }
};
</script>
